@media (max-width: 1200px) {
  .graph {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}
@media (max-width: 1000px) {
  .app {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .app__left {
    padding: 0;
    margin-bottom: 2rem;
  }
  .img {
    margin-top: 40px;
    margin-bottom: 40px;
  }
  .app__right {
