            flex-direction: column;
  }
  .app__header {
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center !important;
            justify-content: center !important;
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
  }
  .app__right {
    padding: 0;
  }
  .graph {
    padding: 0;
  }
}

