@-webkit-keyframes showTopText {
  0% {
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
  }
  30%,
  60% {
    -webkit-transform: translate3d(0, 25%, 0);
            transform: translate3d(0, 25%, 0);
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}
@keyframes showTopText {
  0% {
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
  }
  30%,
  60% {
    -webkit-transform: translate3d(0, 25%, 0);
            transform: translate3d(0, 25%, 0);
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

.animated-title {
  height: 45px;
  left: 0;
  position: relative;
  width: 310px;
}
.animated-title > div {
  height: 100%;
  overflow: hidden;
  position: absolute;
  width: 100%;
}
.animated-title > div div {
  font-size: 40px;
  position: absolute;
}
.animated-title > div.text-top {
  border-bottom: 7px solid;
}
.animated-title > div.text-top div {
  -webkit-animation: showTopText 3s infinite;
          animation: showTopText 3s infinite;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-fill-mode: forwards infinite;
          animation-fill-mode: forwards infinite;
  bottom: 0;
  -webkit-transform: translate(0, 100%) infinite;
          transform: translate(0, 100%) infinite;
}

